   .contwo {
    display: grid;
    grid-template-columns: 3rem 3rem 3rem 3rem 3rem 3rem;
    /* background-color: #ffffff; */
    padding: 1rem;
    border-radius: .01rem;
    row-gap: 0.1rem;
    padding-top: 2%;
    font-size: calc(0.3rem + 0.9vw);        
  }
   .con {
    display: grid;
    grid-template-columns: 3.5rem 3.5rem 3.5rem 3.5rem 3.5rem ;
    /* background-color: #ffffff; */
    padding: 1rem;
    border-radius: 5rem;
    row-gap: 0.1rem;
    padding-top: 2%;
    font-size: calc(0.3rem + 0.9vw);        
  }

  .item {
    background-color: #80cbc4;
    border: 1px solid #fff;
    padding: 0.5rem;
    font-size: 1rem;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    text-align: center;     
  }
  
  .box-shadow {
    font-family: cursive, sans-serif;
    background-color:  #fadcab;
    color: black;
    padding: 10px;
    border-radius: 4px;
    box-shadow: 2px 2px 20px 10px #e9bea3;   
  }
   
.red-text {
   font-size: 3vh;
      
  }  


  @media only screen and (max-width: 900px) { 
    /* tab */
    .inputbox{
     background-color: red;
     height: 5%;
     border-width: 1vh;
     font-size: 5vh;
         }
  }
  